.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  z-index: 99;
}
.container .header {
  position: relative;
  height: 200px;
  overflow: hidden;
}
.container .header .bg-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-filter: blur(36px);
  filter: blur(36px);
  transform: scale(1.1);
}
.container .header .info-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 30px 20px 0;
}
.container .header .info-wrapper .left {
  width: 120px;
  height: 120px;
}
.container .header .info-wrapper .left img {
  width: 120px;
  height: 120px;
}
.container .header .info-wrapper .right {
  color: #fff;
  flex-grow: 1;
  margin-left: 10px;
  font-weight: bold;
  overflow: hidden;
}
.container .header .info-wrapper .right .title {
  font-size: 18px;
}
.container .header .info-wrapper .right .name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
  margin: 10px 0;
}
.container .header .info-wrapper .right .other {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  color: #eee;
}
.container .navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 10px;
  color: #333;
}
.container .navbar .cat {
  font-size: 16px;
}
.container .navbar .list {
  color: #d3d4da;
}
.container .navbar .list span {
  padding: 0 7px;
  line-height: 1;
  font-size: 14px;
  color: #333;
}
.container .navbar .list span:last-child {
  padding-right: 0;
}
.container .loading {
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #333;
}
