.navbar {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 44px;
  line-height: 44px;
  font-size: 18px;
  z-index: 99;
}
.navbar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 44px;
  background-color: hsla(0, 0%, 4%, 0.2);
}
.navbar .iconfont {
  position: absolute;
  z-index: 10;
  left: 15px;
  font-size: 25px;
  color: #fff;
}
.navbar .title {
  position: absolute;
  left: 15%;
  width: 70%;
  height: 44px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
