.list ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 5px;
}
.list ul li {
  width: 49%;
  margin-bottom: 10px;
}
.list ul li a {
  display: block;
}
.list ul li a .img-box {
  position: relative;
}
.list ul li a .img-box img {
  width: 100%;
  border-radius: 5px;
}
.list ul li a .img-box p {
  position: absolute;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
}
.list ul li a .img-box .playCount {
  right: 5px;
  top: 5px;
}
.list ul li a .img-box .user {
  left: 5px;
  bottom: 5px;
}
.list ul li a .name {
  height: 35px;
  margin-top: 5px;
  line-height: 1.2;
  overflow: hidden;
  color: #333;
}
